import React, { useEffect, useState } from 'react'
import './App.css'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import Login from '../components/Authentication/Login/Login'
import Register from '../components/Authentication/Register/Register'
import Logout from '../components/Authentication/Logout/Logout'
import ForgotPassword from '../components/Authentication/ForgotPassword/ForgotPassword'
import Leaderboards from '../containers/Pages/Leaderboards/Leaderboards'
import Home from '../containers/Pages/Home/Home'
import Puzzle from '../containers/Pages/Puzzle/Container/Puzzle'
import Layout from '../components/Navigation/Layout/Layout'
import * as actions from '../store/actions/index'
import Notifier from '../components/Notifications/Notifier'
import { BrowserRouter as Router, Route, Redirect, Switch } from 'react-router-dom'
import Test from './Pages/TestingStyledComponents/Test'

export const ThemeToggleContext = React.createContext({})
export const ThemeContext = React.createContext({})

const App = ({ isAuthenticated, onTryAutoSignup }) => {
    useEffect(() => {
        onTryAutoSignup()
    }, [])

    let routes = (
        <Switch>
            <Route path='/' exact component={Home} />
            <Route path='/puzzle' exact component={Puzzle} />
            <Route path='/leaderboards' exact component={Leaderboards} />
            <Route path='/auth' component={Login} />
            <Route path='/register' component={Register} />
            <Route path='/forgotpassword' component={ForgotPassword} />
            <Route path='/test' component={Test} />
            <Redirect to='/' />
        </Switch>
    )

    if (isAuthenticated) {
        routes = (
            <Switch>
                <Route path='/' exact component={Home} />
                <Route path='/puzzle' exact component={Puzzle} />
                <Route path='/leaderboards' exact component={Leaderboards} />
                <Route path='/logout' component={Logout} />
                <Redirect to='/' />
            </Switch>
        )
    }

    const [theme, setTheme] = useState('dark')
    const styles = {
        background: {
            light: {
                color: 'white',
            },
            dark: {
                color: '#263859',
            },
        },
        navbarText: {
            light: {
                color: '#959595',
            },
            dark: {
                color: '#7045af',
            },
        },
        button: {
            light: {
                backgroundColor: 'white',
                color: 'blue',
            },
            dark: {
                backgroundColor: 'grey',
                color: 'white',
            },
        },
    }

    if (theme === 'dark') {
        document.body.style.backgroundColor = '#182952'
    } else {
        document.body.style.backgroundColor = 'white'
    }

    return (
        <div>
            <Notifier />
            <ThemeToggleContext.Provider
                value={{
                    setTheme,
                }}>
                <ThemeContext.Provider
                    value={{
                        theme,
                    }}>
                    <Layout isLoggedIn={isAuthenticated}>{routes}</Layout>
                </ThemeContext.Provider>
            </ThemeToggleContext.Provider>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        isAuthenticated: state.auth.token !== null,
        wasLoginError: state.auth.error,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onTryAutoSignup: () => dispatch(actions.authCheckState()),
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App))
